/* lato-latin-100-normal*/
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-display: swap;
  font-weight: 100;
  src: url('./files/lato-latin-100-normal.woff2') format('woff2'), url('./files/lato-latin-100-normal.woff') format('woff');
  
}
