// A bit of SCSS for the accessible tables. It really doesn't do much.

$breakpoint: 767px !default;

*[role='cell'] > .cell-content{
    display:inline;
    margin:0;
    padding:0;
}

@media (max-width: $breakpoint){
  table{
    thead{
      display:none;
    }
    thead + tbody {
      display:block;
      tr{
        display: block;
      }
      td{
        display:flex;
        > span{
          display:block;
        }
      }
      td {
        text-align: left !important;
      }

      td:before {
        content: attr(data-value);
        font-weight: bold;
        margin-right: .5em;
      }
    }
  }
}
